@mixin background($address) {
    background-image: url($address);
    background-repeat: no-repeat;
    background-size: cover;
}

@mixin flex($direction: row, $wrap: wrap, $align: center, $justify: space-around) {
    display: flex;
    flex-direction: $direction;
    flex-wrap: $wrap;
    align-items: $align;
    justify-content: $justify;
}