@import 'mixins';
@import 'reset';
@import 'variables';

body{
  @include background('../images/bg.jpg');
}

div.container {
    max-width: 600px;
    margin: 5% auto;
    background-color: rgba(0, 0, 0, .6);
    padding: 20px;
    border-radius: 5%;

    .header {
        @include flex;
        margin-bottom: 20px;

        h1 {
            text-align: center;
            padding: 1.5rem 0;
            flex-basis: 100%;
            text-shadow: 2px 2px 2px black;
            color: $orange;

            span {
                color: red;
            }
        }

        form {
            input {
                padding: 10px;
                margin-right: 10px;

                &[type='submit'] {
                    background: lighten($orange, 15%);
                }
            }
        }
    }

    // show results
    div.show {
        @include flex($justify: space-between);
        margin: 30px 0 40px;
        background-color: rgba(0, 0, 0, .5);

        section {
            text-align: center;
            flex-basis: 22%;
            height: 100%;
            padding: 25px 0;

            @include flex($direction: column, $justify: space-between);

            p {
                font-size: 1.4rem;
                margin: 5px 0;
                color: black;

                &.confirmed, &.confirmed + .label {
                    color: green;
                }

                &.recovered, &.recovered + .label {
                    color: $blue;
                }

                &.sick, &.sick + .label {
                    color: $red;
                }

                &.deaths, &.deaths + .label {
                    color: $orange;
                }
            }
        }
    }

    // chart and update
    div.show2 {
        div.footer {
            display: flex;
            justify-content: space-between;
            .date {
                margin-top: 5px;
                color: $orange;
            }
            .clear {
                padding: 5px 10px;
                margin-top: 10px;
                background: $gray;

                &:hover {
                    background: darken($gray, 10%);
                }
                &:active {
                    background: darken($gray, 30%);
                    color: $orange;
                }
            }
        }
    }
}